* {
  box-sizing: border-box;
  border: solid red 1px;
}
header .container {
  display: flex;
  align-items: center;
  justify-content: center;
}
header .container img {
  width: 10vmin;
  height: 10vmin;
  margin-right: 1vmin;
}
header .container h1 {
  margin-left: 1vmin;
}
section #generalInfo {
  display: flex;
  align-items: center;
}
section #generalInfo > div:not(.photo) {
  display: grid;
  grid-template-columns: 1fr 2fr;
}
section #generalInfo > div:not(.photo) label {
  display: flex;
  justify-content: space-between;
  width: 100px;
}
section #generalInfo > div:not(.photo) input {
  width: 0;
  display: block;
  padding: 0;
  transition-property: width;
  transition-duration: 2s;
  outline: none;
  border: none;
  border-bottom: 2px solid green;
}
section #generalInfo > div:not(.photo) input:focus,
section #generalInfo > div:not(.photo) input.active {
  width: 100%;
}
section #generalInfo .photo {
  width: 20vmin;
  height: 25vmin;
  background-image: url('images/emptyAvatar.jpg');
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
section #generalInfo .photo input {
  display: none;
}
